[class$=__contenedor] {
  max-width: 75rem;
  width: 90%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.texto-degradado {
  color: transparent;
  background: linear-gradient(to right, #e8505b 0%, #455a64 100%);
  background-clip: text;
}

/** Media Queries **/
html {
  box-sizing: border-box;
  font-size: 14px;
}

*, *:after, *:before {
  box-sizing: inherit;
}

@media (min-width: 400px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 1024px) {
  html {
    font-size: 18px;
  }
}
body {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  font-size: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
}

img,
video,
audio {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
}

h1, h2, h3 {
  margin: 0 0 3.125rem 0;
  line-height: 1.2;
  font-size: clamp(1.56rem, 1vw + 1.31rem, 2.11rem);
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 700;
}

h3 {
  font-weight: 700;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

blockquote {
  margin: 0;
}

.header {
  padding: 3.125rem 0;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media (min-width: 1024px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra__logo {
  width: 18.75rem;
}
.barra__navegacion {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 1024px) {
  .barra__navegacion {
    flex-direction: row;
    align-items: center;
  }
}
.barra__link {
  font-weight: 700;
}
.barra__link--registrar {
  background-color: #e8505b;
  padding: 0.625rem 1.875rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.barra__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .barra__link--registrar {
    width: auto;
  }
}

.delivery__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.25rem;
}
@media (min-width: 768px) {
  .delivery__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}
.delivery__contenido {
  background-color: #e8505b;
  padding: 6.25rem 2.5rem;
}
.delivery__heading {
  color: #FFFFFF;
}
.delivery__formulario {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.delivery__label {
  display: block;
  color: #FFFFFF;
  font-size: clamp(1.25rem, 0.61vw + 1.1rem, 1.58rem);
}
.delivery__input {
  width: 100%;
  padding: 0.625rem;
}
.delivery__btn {
  font-size: clamp(1.25rem, 0.61vw + 1.1rem, 1.58rem);
  padding: 0.625rem;
  font-weight: 900;
  width: 100%;
  background-color: #455a64;
  padding: 0.625rem 1.875rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.delivery__btn:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .delivery__btn {
    width: auto;
  }
}
.delivery__img {
  width: 18.75rem;
}
@media (min-width: 1024px) {
  .delivery__img {
    width: 100%;
  }
}

.pasos {
  margin-top: 3.125rem;
}
.pasos__wave {
  width: 100%;
}
.pasos__backgound {
  background-color: #e8505b;
  margin-top: -1px;
}
.pasos__heading {
  color: #FFFFFF;
}
.pasos__span {
  display: block;
  font-size: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
}
.pasos__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .pasos__grid {
    flex-direction: row;
  }
}
.pasos__paso {
  width: 100%;
  transition-property: transform;
  transition-duration: 0.3s;
}
.pasos__paso:hover {
  transform: rotate(-4deg) scale(1.2);
}

.testimoniales {
  margin-top: 3.125rem;
}
@media (min-width: 1024px) {
  .testimoniales__contenedor {
    display: grid;
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonio {
  max-width: 700px;
  margin: 0 auto 1.25rem auto;
  color: #FFFFFF;
  background-color: #455a64;
  border-radius: 0.625rem;
  padding: 1.25rem;
}
@media (min-width: 1024px) {
  .testimonio {
    margin: 0;
    max-width: unset;
  }
  .testimonio:first-of-type {
    grid-column: 1/4;
    grid-row: 1/2;
  }
  .testimonio:nth-child(2) {
    grid-column: 4/5;
    grid-row: 1/2;
  }
  .testimonio:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonio:nth-child(4) {
    grid-column: 1/3;
    grid-row: 2/3;
  }
  .testimonio:nth-child(5) {
    grid-column: 3/5;
    grid-row: 2/3;
  }
}
.testimonio:last-of-type {
  margin-bottom: 0;
}
.testimonio__header {
  display: flex;
  align-items: center;
  gap: 1.25rem;
}
.testimonio__img {
  border-radius: 50%;
}
.testimonio__nombre {
  font-weight: 900;
  font-size: clamp(1.25rem, 0.61vw + 1.1rem, 1.58rem);
  margin: 0;
}
.testimonio__dicho {
  display: flex;
  margin: 1.25rem 0;
  align-items: center;
  margin: 0;
}
.testimonio__dicho::before {
  content: ' " ';
  font-size: 6.25rem;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  opacity: 0.2;
}